import React, { useState, useEffect } from 'react'
import { Form, Input, Button, Card, Divider, Space } from 'antd';
import Api from '../../helpers/api';
import { message } from "antd";

import {
    ReloadOutlined,
    ArrowLeftOutlined,
    SaveOutlined
} from '@ant-design/icons';
import handleApiError from '../../helpers/handleApiError';

const api = new Api();

const FormPage = (props) => {

    const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    const [form] = Form.useForm();

    useEffect(() => {
        const { add, edit } = props

        if (edit) {
            getEditData()
        }
    }, []);

    const onFinish = (data) => {
        setLoading(true)

        let apiPost
        //handling if form add
        if (props.add) {
            apiPost = api.storePlant(data)
        } else if (props.edit) {
            //handling if form edit
            const { id } = props.match.params
            apiPost = api.updatePlant(id, data)
        }

        apiPost.then(response => {
            console.log(response)
            setLoading(false)
            // console.log(response.data);
            message.success('save data success');
            props.history.goBack();
        }).catch(error => {
            setLoading(false)
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                setErrors(errors)
                // message.error(message);
            }
            handleApiError(error, props)
        });
    };

    const getEditData = () => {
        const { id } = props.match.params

        api.viewPlant(id)
            .then(response => {
                const { code, name, description } = response.data
                form.setFieldsValue({
                    code,
                    name,
                    description,
                });
            }).catch(error => {
                handleApiError(error, props)
            })
    }

    const handleReset = () => {
        form.setFieldsValue({
            code: '',
            name: '',
            description: '',
        });
    }

    return (
        <Card>
            <Form name="complex-form" onFinish={onFinish} labelCol={{ span: 4 }} wrapperCol={{ span: 8 }}
                form={form}>

                <Form.Item name="code" label="Code"
                    validateStatus={errors && errors.code ? 'error' : undefined}
                    help={errors ? errors.code : null}
                    rules={[{ required: true, message: 'Please input code' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="name" label="Name"
                    validateStatus={errors && errors.name ? 'error' : undefined}
                    help={errors ? errors.name : null}
                    rules={[{ required: true, message: 'Please input name' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="description" label="Description"
                    validateStatus={errors && errors.description ? 'error' : undefined}
                    help={errors ? errors.description : null}
                    rules={[{ required: true, message: 'Please input description' }]}>
                    <Input.TextArea />
                </Form.Item>

                <p><span style={{ color: '#f5222d', fontSize: 14 }}>*</span> Required</p>
                <Divider />

                <Space>
                    <Button onClick={() => props.history.goBack()}
                        icon={<ArrowLeftOutlined />}
                    >Back</Button>

                    <Button onClick={() => handleReset()}
                        icon={<ReloadOutlined />}
                    >Reset</Button>

                    <Button type="primary" htmlType="submit" loading={loading}
                        icon={<SaveOutlined />}
                    >
                        Save
                    </Button>
                </Space>

            </Form>
        </Card >
    )
}

export default FormPage;